import EmployeeGreeting from "@/components/shared/employee-greeting";

import {
    Item,
    ItemActions,
    ItemContent,
    ItemDescription,
    ItemTitle,
} from "@/components/ui/item"
import { ChevronRightIcon } from "lucide-react"

const EDITOR_LINKS = [
    {
        title: "Напишем контент",
        description: "Переходи на страницу написания контента",
        href: "editor/write",
    },
    {
        title: "Проверим черновики",
        description: "Переходи на страницу проверки черновиков",
        href: "editor/drafts",
    }
]

export default function EditorPage() {
    return (
        <div className="flex flex-col gap-6 md:gap-8 lg:gap-10">
            <EmployeeGreeting />
            <div className="grid md:grid-cols-2 gap-4 md:gap-6 lg:gap-8">
                {EDITOR_LINKS.map((link, index) => (
                    <EditorLinks key={index} link={link} />
                ))}
            </div>
        </div>
    );
}

const EditorLinks = ({ link }: { link: typeof EDITOR_LINKS[number] }) => {
    return (
        <Item asChild>
            <a href={link.href}>
                <ItemContent>
                    <ItemTitle>{link.title}</ItemTitle>
                    <ItemDescription>
                        {link.description}
                    </ItemDescription>
                </ItemContent>
                <ItemActions>
                    <ChevronRightIcon className="size-4" />
                </ItemActions>
            </a>
        </Item>
    );
}